    url('../fonts/icons.ttf?g8m8kf') format('truetype'),
    url('../fonts/icons.woff?g8m8kf') format('woff'),
    url('../fonts/icons.svg?g8m8kf#icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="_icon-"], [class*=" _icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
